<template>
  <div class="promotions container">
    <template v-if="promoList.length">
      <div v-for="(item,index) in promoList" :key="index" class="promotions__item">
        <a :href="item.link" :target="item.is_blank?'_blank':'_self'" class="promotions__img-wrapper default-bg-small">
          <img class="promotions__img" v-lazy="item.pic" :alt="item.title">
        </a>
        <p class="promotions__title">{{item.title}}</p>
        <p class="promotions__sub-title">{{item.label}}</p>
        <p class="promotions__date">{{item.date}}</p>
      </div>
    </template>
    <template v-else>
      <div v-for="index in 3" :key="index" class="promotions__item">
        <a class="promotions__img-wrapper default-bg-small"></a>
        <p class="promotions__title">--</p>
        <p class="promotions__sub-title">--</p>
        <p class="promotions__date">-</p>
      </div>
    </template>
  </div>
</template>

<script>
import moment from 'moment'

export default {
  name: 'promotions',
  data() {
    return {
      promoList: []
    }
  },
  created() {
    this.getPromoList()
  },
  methods: {
    getPromoList() {
      const url = '/_promo/promo_list_new.txt'
      this.$api.getWmsStatic(url).then(({ data }) => {
        let promo
        let list = []
        const nowTime = Date.now()
        if (Array.isArray(data)) {
          data = data.filter(item => {
            if (item.retain1 === '1') {
              if (item.time_begin === item.time_end) return +item.time_begin === 0
              return item.time_end * 1000 > nowTime
            }
            return false
          })
          data = data.map(item => {
            promo = {
              title: item.title,
              label: item.label,
              link: item.link ? `/h5${item.link}` : null,
              is_blank: +item.is_blank === 1,
              pic: `${window.dynamicConfig.wmsServer}/${item.pic}`
            }
            if (+item.time_begin === 0) {
              promo.date = '永久有效'
            } else {
              promo.date = `${this.formatDate(item.time_begin)} - ${this.formatDate(item.time_end)}`
            }
            return promo
          })
          list = data
        }
        this.promoList = list
      })
    },
    formatDate(timeStamp) {
      return moment(timeStamp * 1000).format('YYYY年MM月DD日')
    }
  }
}
</script>

